<template>
  <v-container>
    <v-col>
      <v-lazy
        transition="scroll-x-transition"
        :options="{
            threshold: .5
          }"
        min-height="700"
      >
        <v-card dark elevation="10">
          <v-card-title class="text-center justify-center py-6" dark>
            <h1>Technical Skill</h1>
          </v-card-title>

          <v-tabs v-model="tab" color="teal accent-2" grow>
            <v-tab
              v-for="(item,index) in items"
              :key="index"
              @click="changeTabItem(index)"
            >{{ item }}</v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <v-tab-item v-for="item in items" :key="item">
              <v-timeline>
                <v-timeline-item
                  v-for="(item , index) in tabItems"
                  :key="index"
                  large
                  color="white"
                  class="animated rollIn"
                >
                  <template v-slot:icon>
                    <v-avatar>
                      <img :src="item.src" />
                    </v-avatar>
                  </template>

                  <v-card class="elevation-2">
                    <v-card-title class="headline">{{item.title}}</v-card-title>
                    <v-card-text>
                      <v-progress-linear
                        v-model="item.skill"
                        :color="item.color"
                        height="25"
                        reactive
                      >
                        <template v-slot="{ value }">
                          <strong>{{ Math.ceil(value) }}%</strong>
                        </template>
                      </v-progress-linear>
                    </v-card-text>
                  </v-card>
                </v-timeline-item>
              </v-timeline>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-lazy>
    </v-col>
  </v-container>
</template>

<script>
import "animate.css";

export default {
  mounted() {
    this.tabItems = this.web;
  },
  data() {
    return {
      percentage: 0,
      tab: null,
      skill: 20,
      items: [
        "Web Development",
        "Machine Learnning Development",
        "Other Programming"
      ],
      tabItems: [],
      web: [
        {
          src:
            "https://www.technointrend.com/wp-content/uploads/2013/05/html_5_logo_small.jpg",
          title: "HTML",
          skill: 65,
          color: "orange darken-2"
        },
        {
          src:
            "https://d1yjjnpx0p53s8.cloudfront.net/styles/logo-thumbnail/s3/082014/js1_0.png?itok=9fCD5b30",
          title: "Java Script",
          skill: 60,
          color: "yellow darken-1"
        },
        {
          src: "https://logodix.com/logo/1758977.png",
          title: "Django",
          skill: 50,
          color: "teal lighten-2"
        },
        {
          src: "https://vuejs.org/images/logo.png",
          title: "Vue",
          skill: 50,
          color: "green lighten-1"
        },
        {
          src: "https://image.flaticon.com/icons/png/512/28/28968.png",
          title: "JSP",
          skill: 70,
          color: "blue-grey lighten-2"
        },
        {
          src:
            "https://f0.pngfuel.com/png/251/608/blue-and-white-10-print-php-png-clip-art-thumbnail.png",
          title: "PHP",
          skill: 50,
          color: "light-blue lighten-2"
        }
      ],
      machine: [
        {
          src:
            "https://journeyofthegeekcom.files.wordpress.com/2019/01/python_logo.png",
          title: "Python",
          skill: 55,
          color: "lime lighten-1"
        },
        {
          src:
            "https://thaikeras.com/wp-content/uploads/2018/05/49D40034-AF62-4D6D-8076-CCF2A1ECED71.jpeg",
          title: "Keras",
          skill: 45,
          color: "red darken-1"
        }
      ],
      other: [
        {
          src:
            "https://upload.wikimedia.org/wikipedia/commons/1/18/ISO_C%2B%2B_Logo.svg",
          title: "C,C++",
          skill: 60,
          color: "indigo darken-1"
        },
        {
          src:
            "https://i.pinimg.com/originals/f1/ea/a7/f1eaa7278f64e27128e062a3de918265.png",
          title: "Java",
          skill: 70,
          color: "red lighten-3"
        },
      ]
    };
  },
  methods: {
    changeTabItem(index) {
      console.log("index: " + index);
      if (parseInt(index) == 0) {
        this.tabItems = this.web;
      }
      if (parseInt(index) == 1) {
        this.tabItems = this.machine;
      }
      if (parseInt(index) == 2) {
        this.tabItems = this.other;
      }
    }
  }
};
</script>

<style scoped>
h1 {
  font-family: "Patrick Hand", cursive;
  font-size: 4vw;
}
</style>