<template>
  <v-navigation-drawer
        v-model="$store.state.drawer"
        color="black"
        :mini-variant="miniVariant" 
        dark
        app
      >
        <v-list
          dense
          nav
          class="py-0"
        >
          <v-list-item two-line :class="miniVariant && 'px-0'" >
            <v-list-item-avatar >
              <img src="@/assets/image/IMG_0208.png">
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title >Supakorn Klaiklain</v-list-item-title>
              <v-list-item-subtitle>Web developer</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-divider></v-divider>

          <v-list-item
            v-for="item in items"
            :key="item.title"
            :to="item.to"
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <v-footer class="justify-center" fixed color="blue-grey darken-3">
        <v-btn icon @click.stop="miniVariant = !miniVariant">
          <v-icon>mdi-{{ `chevron-${miniVariant ? 'right' : 'left'}` }}</v-icon>
        </v-btn>
      </v-footer>
      </v-navigation-drawer>

     
</template>

<script>
export default {
  mounted() {
     this.onResponsiveInverted ()
  },
  data() {
    return {
      color:"primary",
      drawer: true,
      miniVariant: false,
      items: [
        { title: "Home", icon: "mdi-home" ,to:"/"},
        { title: "About me", icon: "mdi-account",to:"/about" },
        
      ]
    };
  },
  methods:{
    onResponsiveInverted () {
            if (window.innerWidth < 1264) {
                this.$store.state.drawer = false
            } else {
                this.$store.state.drawer = true
            }
        }
  }
};
</script>

