<template>

  <v-app-bar v-if="responsive" flat collapse-on-scroll dark  app>
    <v-app-bar-nav-icon large class="d-lg-none and-up" @click.stop="$store.state.drawer = !$store.state.drawer" />

    <v-toolbar-title class="hidden-lg-and-down" />
    
    <v-spacer></v-spacer>
    
    <v-toolbar-title style="font-family:'Franklin Gothic Medium'">{{$route.name}}</v-toolbar-title>
    
  </v-app-bar>
</template>

<script>
export default {
    data() {
        return {
            responsive:false,
            title:"Home"
        }
    },
    mounted() {
        setInterval(() => {
            this.onResponsiveInverted()
        }, 100);
        
    },
    methods:{
        onResponsiveInverted () {
            if (window.innerWidth < 1264) {
                this.responsive = true
            } else {
                this.responsive = false
            }
        }
    }
    
};
</script>

